@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

:root {
  --font-size: 14px;
  --main-font: 'Montserrat', sans-serif;
  --roboto: 'Roboto', sans-serif;
  --main-color: #11257A;
  --dark-main-color: #0A1547;
  --grey-color: #7A7878;
  --dark-grey-color: #474747;
  --placeholder: #999999;
  --container: 1160px;
}

html {
  font-size: var(--font-size);
}

body {
  margin: 0;
  font-family: var(--main-font);
  color: var(--main-color);
  background: #fff;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: var(--dark-main-color) #d0d6dc;
}

body::-webkit-scrollbar-thumb {
  background: var(--dark-main-color);
}

body::-webkit-scrollbar {
  width: 6px;
}

body::-webkit-scrollbar-track {
  background: #d0d6dc;
}

body.no-scroll {
  overflow: hidden;
}

main {
  overflow-x: hidden;
}

ul,
li {
  display: block;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

* {
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  text-decoration: none;
  outline: none;
}

button {
  padding: 0;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type='number'],
input[type="number"]:hover,
input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

.container {
  position: relative;
  max-width: var(--container);
  width: 100%;
  margin: 0 auto;
}

.pointer {
  cursor: pointer;
}

.upper {
  text-transform: uppercase;
}

._f-space {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

._f-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

._f-wrap {
  display: flex;
  flex-wrap: wrap;
}

._f-column {
  display: flex;
  flex-direction: column;
}

._f-align-center {
  display: flex;
  align-items: center;
}

.loader::after {
  content: '';
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: rgba(255, 255, 255, 0.6) url("../img/icons/spinner.svg") center/200px no-repeat;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 700;
  font-size: 14px;
  line-height: 160%;
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
  border-radius: 4px;
  cursor: pointer;
  transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out;
}

.btn:hover,
.btn:focus-visible {
  background-color: var(--dark-main-color);
  border-color: var(--dark-main-color);
}

.common-range {
  max-width: calc(100% - 10px);
  margin: 0 auto;
  height: 2px;
  border-radius: 0;
  border: none;
  box-shadow: none;
}

.common-range .noUi-connects {
  background-color: var(--main-color);
  opacity: .4;
}

.common-range .noUi-connects > .noUi-connect {
  background-color: var(--grey);
}

.common-range .noUi-handle {
  top: -2.6px;
  right: 0;
  width: 7px;
  height: 7px;
  background-color: var(--main-color);
  border: none;
  border-radius: 50%;
  box-shadow: none;
  cursor: pointer;
}

.common-range .noUi-handle::before,
.common-range .noUi-handle::after {
  content: none;
}

.common-range .noUi-handle.noUi-handle-lower {
  right: -7px;
}

.disabled {
  opacity: .6;
  pointer-events: none;
}

.validate-border {
  border-color: red !important;
}

.tablet {
  display: none !important;
}

.tablet_two {
  display: none;
}

#overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: none;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
}

.select {
  position: relative;
}

.select.open .select-title::after {
  transform: rotate(180deg);
}

.select.open .select-content {
  opacity: 1;
  transform: scale(1);
}

.select-title {
  position: relative;
  display: flex;
  align-items: center;
  height: 46px;
  padding: 0 42px 0 16px;
  color: var(--placeholder);
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  background-color: #fff;
  border: 1px solid #E5E5E5;
  border-radius: 4px;
  cursor: pointer;
}

.select-title::after {
  content: '';
  position: absolute;
  top: 0;
  right: 8px;
  bottom: 0;
  display: block;
  width: 24px;
  height: 24px;
  margin: auto;
  background: url("../img/icons/select-arrow.svg") center/8px 5px no-repeat;
  transition: transform .3s ease-in-out;
}

.select-content {
  position: absolute;
  top: 45px;
  right: 0;
  left: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 8px 0;
  background-color: #fff;
  border: 1px solid #E5E5E5;
  border-radius: 0 0 2px 2px;
  box-shadow: 0 2px 6px 1px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transform: scale(0);
  overflow-y: scroll;
  transition: opacity .3s ease-in-out;
  scrollbar-width: thin;
  scrollbar-color: #a1adb9 #d0d6dc;
}

.select-content::-webkit-scrollbar-thumb {
  background: #a1adb9;
}

.select-content::-webkit-scrollbar {
  width: 6px;
}

.select-content::-webkit-scrollbar-track {
  background: #d0d6dc;
}

.select-content__wrapper {
  display: flex;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color .1s ease-in-out;
}

.select-content__wrapper:hover {
  background-color: rgba(138, 153, 168, 0.15);
}

.select-content__wrapper input {
  display: none !important;
}

.select-content__radio {
  color: rgba(0, 0, 0, 0.9);
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.015em;
}

.hero {
  position: relative;
}

.hero .container {
  max-width: 1640px;
}

.prev_nav_scroll #header {
  position: fixed;
  top: 0;
  height: 63px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
  opacity: 0;
  transform: scaleY(0);
}

.prev_nav_scroll #header .logo .svg-ico {
  color: var(--main-color);
}

.prev_nav_scroll #header .nav-menu__list-item a {
  color: var(--main-color);
}

.prev_nav_scroll #header .header-phone {
  color: var(--main-color);
}

.prev_nav_scroll #header .hamburger.open .hamburger-ico {
  border-color: transparent;
}

.prev_nav_scroll #header .hamburger-ico {
  border-color: var(--main-color);
}

.prev_nav_scroll #header .hamburger-ico::after,
.prev_nav_scroll #header .hamburger-ico::before {
  background-color: var(--main-color);
}

.nav_scroll #header {
  opacity: 1;
  transform: scaleY(1);
}

#header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  height: 92px;
  background-color: transparent;
  transform-origin: 100% 0;
  transition: background-color .3s ease-in-out, transform .3s ease-in-out;
}

#header .container {
  justify-content: space-between;
}

#header .logo {
  color: #fff;
  transition: color .3s ease-in-out;
}

#header .nav-menu .logo-mobile {
  display: none;
}

#header .nav-menu__list {
  display: flex;
  align-items: center;
  gap: 32px;
}

#header .nav-menu__list-item a {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #fff;
  font-size: 14px;
  line-height: 160%;
  transition: opacity .3s ease-in-out;
}

#header .nav-menu__list-item a:hover {
  opacity: .6;
}

#header .nav-menu__list-item_last {
  display: none;
}

#header .nav-menu__list-item_last .btn-call {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 201px;
  height: 40px;
  background-color: #fff;
  border: 1px solid var(--main-color);
  border-radius: 4px;
}

#header .header-phone {
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  line-height: 160%;
  transition: opacity .3s ease-in-out;
}

#header .header-phone:hover {
  opacity: .6;
}

.hamburger,
.close {
  position: relative;
  right: 0;
  display: none;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.hamburger.open,
.close.open {
  z-index: 2;
}

.hamburger.open .hamburger-ico,
.hamburger.open .close-ico,
.close.open .hamburger-ico,
.close.open .close-ico {
  border-color: transparent;
}

.hamburger.open .hamburger-ico::after,
.hamburger.open .hamburger-ico::before,
.hamburger.open .close-ico::after,
.hamburger.open .close-ico::before,
.close.open .hamburger-ico::after,
.close.open .hamburger-ico::before,
.close.open .close-ico::after,
.close.open .close-ico::before {
  background-color: var(--main-color);
}

.hamburger.open .hamburger-ico::after,
.hamburger.open .close-ico::after,
.close.open .hamburger-ico::after,
.close.open .close-ico::after {
  transform: rotate(45deg);
}

.hamburger.open .hamburger-ico::before,
.hamburger.open .close-ico::before,
.close.open .hamburger-ico::before,
.close.open .close-ico::before {
  transform: rotate(-45deg);
}

.hamburger.open .hamburger-ico::after,
.hamburger.open .hamburger-ico::before,
.close.open .hamburger-ico::after,
.close.open .hamburger-ico::before {
  background-color: #fff;
}

.hamburger-ico,
.close-ico {
  position: relative;
  width: 20px;
  height: 14px;
  border-bottom: 2px solid #fff;
  border-top: 2px solid #fff;
  transition: all .3s ease;
  cursor: pointer;
}

.hamburger-ico::before,
.hamburger-ico::after,
.close-ico::before,
.close-ico::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  background-color: #fff;
  width: 100%;
  height: 2px;
  margin-top: -1px;
  transition: all .3s ease;
}

.webp .main-hero {
  background-image: url("../img/hero-new.png") !important;
}

.main-hero {
  height: 100vh;
  color: #fff;
  background: #32363a url("../img/hero-new.png") center/cover no-repeat;
}

.main-hero__content-wrap {
  position: relative;
  max-width: 800px;
  padding-left: 30px;
}

.main-hero__content-wrap::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 270px;
  background-color: #2B2D3E;
}

.main-hero__overtitle {
  margin-bottom: 24px;
  font-size: 22px;
  line-height: 130%;
  font-weight: 700;
}

.main-hero__title {
  margin-bottom: 32px;
  font-weight: 700;
  font-size: 54px;
  line-height: 130%;
}

.main-hero__wrap {
  display: flex;
  align-items: center;
  gap: 30px;
}

.main-hero__description {
  max-width: 300px;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}

.main-hero__btn {
  width: 338px;
  height: 56px;
}

.main-hero__scroll-btn {
  position: absolute;
  bottom: -81px;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 19px;
  cursor: pointer;
  gap: 36px;
  transform: translateY(100%);
}

.main-hero__scroll-btn::before {
  content: url("../img/icons/circles.svg");
}

.main-hero__scroll-btn::after {
  content: url("../img/icons/arrow-down.svg");
  margin-bottom: -8px;
}

.main-hero__scroll-btn span {
  color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
  text-transform: uppercase;
  transform: rotate(270deg);
}

.main-hero__list-wrap {
  position: absolute;
  bottom: 63px;
  right: 0;
  max-width: calc(248px * 3 + 8px);
}

.main-hero__list-wrap .arrow-container {
  display: flex;
  gap: 16px;
}

.main-hero__list-wrap .arrow-container .arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color .3s ease-in-out, color .3s ease-in-out;
}

.main-hero__list-wrap .arrow-container .arrow:hover {
  background-color: #fff;
  color: var(--main-color);
}

.main-hero__list-wrap .arrow-container .arrow.arrow-left svg {
  margin-left: -5px;
}

.main-hero__list-wrap .arrow-container .arrow.arrow-right svg {
  margin-right: -3px;
}

.main-hero__list-wrap .arrow-container .arrow.disable {
  color: var(--main-color);
  background-color: #fff;
  pointer-events: none;
  opacity: .5;
}

.main-hero__list-title {
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 600;
  line-height: 130%;
}

.main-hero__list {
  position: relative;
  display: flex;
  gap: 4px;
  margin: 0 0 24px;
  padding-top: 57px;
  overflow-x: scroll;
  scrollbar-width: none;
}

.main-hero__list::-webkit-scrollbar {
  width: 0;
}

.main-hero__list-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex-shrink: 0;
  max-width: 248px;
  width: 100%;
  height: 172px;
  padding: 16px 19px 19.4px 26px;
  background-color: transparent;
  border-radius: 10px;
}

.main-hero__list-item::before,
.main-hero__list-item::after {
  content: '';
  position: absolute;
  left: 0;
}

.main-hero__list-item::after {
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-color: transparent;
  border-radius: 10px;
  transition: background-color .3s ease-in-out;
}

.main-hero__list-item::before {
  top: -56.5px;
  width: calc(100% + 4px);
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2);
}

.main-hero__list-item.active {
  padding-top: 48px;
}

.main-hero__list-item.active::after {
  background-color: #2B2D3E;
}

.main-hero__list-item.active::before {
  top: -57px;
  height: 3px;
  background-color: var(--main-color);
}

.main-hero__list-item.active .main-hero__list-item__top {
  margin-bottom: 16px;
  padding-bottom: 16px;
  color: #fff;
}

.main-hero__list-item.active .main-hero__list-item__top::after {
  display: block;
}

.main-hero__list-item.active .main-hero__list-item__bottom {
  display: block;
}

.main-hero__list-item.active .main-hero__list-item__num {
  opacity: 1;
}

.main-hero__list-item.main-hero__list-item_last {
  background-color: transparent;
  pointer-events: none;
}

.main-hero__list-item > * {
  position: relative;
  z-index: 2;
}

.main-hero__list-item__top {
  position: relative;
  display: flex;
  align-items: center;
  gap: 24px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
  font-weight: 600;
  line-height: 130%;
}

.main-hero__list-item__top::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  display: none;
  width: 102.72px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
}

.main-hero__list-item__bottom {
  display: none;
  font-size: 14px;
  line-height: 130%;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.5);
}

.main-hero__list-item__num {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  opacity: 0;
  transform: translate(18px, -44px);
  transition: opacity .3s ease-in-out;
}

.main-hero__list-item__num img {
  width: 107px;
  height: 98px;
}

.catalog {
  padding: 100px 0;
}

.catalog__title {
  margin-bottom: 24px;
  color: var(--dark-main-color);
  font-weight: 700;
  font-size: 36px;
  line-height: 125%;
}

.catalog__tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 18px;
  margin-bottom: 32px;
  padding-bottom: 32px;
  border-bottom: 1px solid #E5E5E5;
}

.catalog__tabs-item {
  display: flex;
  align-items: center;
  height: 46px;
  padding: 0 24px;
  color: var(--dark-main-color);
  font-weight: 700;
  font-size: 14px;
  line-height: 160%;
  background-color: transparent;
  border: 1px solid #E5E5E5;
  border-radius: 33px;
  cursor: pointer;
  transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out;
}

.catalog__tabs-item.active {
  color: #fff;
  background-color: var(--main-color);
  border-color: var(--main-color);
}

.catalog__tabs-item:hover,
.catalog__tabs-item:focus-visible {
  color: #fff;
  background-color: var(--dark-main-color);
  border-color: var(--dark-main-color);
}

.catalog__filters {
  margin-bottom: 50px;
}

.catalog__filters .filters {
  display: flex;
  gap: 16px;
  width: 100%;
}

.catalog__filters .filters .select {
  max-width: 300px;
  width: 100%;
}

.catalog__filters .filters .select-title {
  height: 46px;
}

.catalog__filters .filters .filters__range-price {
  position: relative;
  max-width: 300px;
  width: 100%;
  max-height: 46px;
}

.catalog__filters .filters .filters__range-price__wrap {
  display: flex;
  align-items: center;
  gap: 2px;
  width: 100%;
  height: 46px;
  padding: 12px 16px;
  border: 1px solid #E5E5E5;
  border-radius: 4px;
}

.catalog__filters .filters .filters__range-price__wrap span {
  color: var(--dark-main-color);
}

.catalog__filters .filters #minPrice,
.catalog__filters .filters #maxPrice {
  width: max-content;
  max-height: 46px;
  padding: 0;
  color: var(--dark-main-color);
  font-size: 14px;
  line-height: 22px;
  background-color: transparent;
  border: 0;
}

.catalog__filters .filters .filters__range-price__range {
  position: absolute;
  bottom: -5px;
  right: 0;
  left: 0;
  max-width: 292px;
  margin: 0 auto;
  transform: translateY(100%);
}

.catalog__filters .filters .filters__range-price__range > div {
  color: var(--placeholder);
  font-size: 8px;
  line-height: 13px;
  letter-spacing: 0.04em;
}

.catalog__filters .filters .filters__btn {
  width: 210px;
  height: 46px;
  margin-left: auto;
}

.catalog__filters .filters .close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.catalog__filters .catalog__btn-show-filters {
  display: none;
}

.catalog__list-slider {
  display: none !important;
  margin: 0 -20px 30px !important;
}

.catalog__list-slider.active {
  display: block !important;
}

.catalog__list-slider .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  color: var(--main-color);
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transform: translateY(-50%);
  cursor: pointer;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;
}

.catalog__list-slider .slick-arrow:hover,
.catalog__list-slider .slick-arrow:focus-visible {
  color: #fff;
  background-color: var(--main-color);
}

.catalog__list-slider .slick-arrow.arrow-left {
  left: -10px;
}

.catalog__list-slider .slick-arrow.arrow-right {
