.modal__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  padding: 50px;
  width: max-content;
  height: max-content;
  background-color: #fff;
  border-radius: 4px;
  cursor: default;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.modal__close::before,
.modal__close::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  background-color: var(--main-color);
  width: 100%;
  height: 2px;
  margin-top: -1px;
  transition: all .3s ease;
}

.modal__close::before {
  transform: rotate(-45deg);
}

.modal__close::after {
  transform: rotate(45deg);
}

